.info1, .info2, .info3, .info4
{
background: #F5EBDB;
border: 1px solid #FFFFFF;
border-radius: 10px;
padding: 25px 50px;
margin: 20px 0;
}
.menu h1 {
	font-weight: 700;
	font-size: 48px;
}

.net h2 {
	font-weight: 700;
	font-size: 28px;
	line-height: 33px;
}
.strong {
	font-weight: 600;
	display: block;
}
.main {
	display: flex;
	justify-content: space-between;
}
.net {
	display: grid;
	width: 100%;
	/* height: 250px; */
	grid-template-areas:
	  "info1 info3"
	  "info2 info3"
	  "postimg info4";

	grid-template-columns: 45% 45%;
	grid-gap: 10%;
}
.info1 {
	grid-area: info1;
}

.info2 {
	grid-area: info2;
}

.info3 {
	grid-area: info3;
}

.info4 {
	grid-area: info4;
}
.postimg {
	grid-area: postimg;	
}